.featuredcontainer {
    display: flex;
    /* justify-content: space-between; */
    max-width: 1024px;
    width: 100%;
    gap: 10px;
    z-index: 1;
    /* border: 2px solid red; */
    flex-direction: column;
}

.featureditems {
    border-radius: 10px;
    /* border: 1px solid red; */
    color: white;
    position: relative;
    display: flex;
    gap: 15px;
    box-sizing: border-box;
    max-width: 1024px;

}

.item {
    flex: 1;
    position: relative;
    box-sizing: border-box;
 

}


.item>img {
    width: 100%;
    object-fit: cover;
    opacity: 1;
    height: 270px;
    box-sizing: border-box;

}

.item>img:hover {
    border: 1px solid orange;
    box-shadow: -1px -20px 152px 25px rgba(0, 0, 0, 0.5) inset;
    -webkit-box-shadow: -1px -20px 152px 25px rgba(0, 0, 0, 0.5) inset;
    -moz-box-shadow: -1px -20px 152px 25px rgba(0, 0, 0, 0.5) inset;
}



.featureditemsTitle {
    position: absolute;
    bottom: 20px;
    left: 19px;
}

@media screen and (max-width:584px) {

    .featureditems {
           /* padding: 10px;  */
           gap: 2px;
    }
    .item {
        flex-wrap: wrap;
    }

    .item>img {
        width: 100%;
        object-fit: cover;
        opacity: 1;
        height: 100px;       
        box-sizing: border-box;
        max-width: 250px;
    
    }

    .featuredcontainer{
        gap:0;
    }
    
.featureditemsTitle, h1, h3{
    font-size: 12px;
    bottom: 10px;
    left: 10px;
    
}
}