.outersearchlistdiv{
    margin:  20px 10px;
    border: 1px solid grey;
    padding: 2px;
    box-shadow: 0.5px 0.5px 1px 1px grey;
 

}
.innerdiv{
    display: flex;
   flex: 3;
   width: 100%;
    
}

.innerdiv>img{
    width: 100%;
    max-width: 250px;
    height: 200px;
}

.content{
    display: flex;
    flex-direction: column;
    gap: 5px;
    flex: 1;
}

.content1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;

}

.Grade{
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 6px;
}

.Grade span:nth-child(2){
    background-color: blue;
    padding: 2px;
    border-radius: 5px 5px 5px 0;
}

.Free_taxi{
    background-color: darkgreen;
    color: white;
    width: 100px;
    font-size: 13px;
    padding: 5px;
    border-radius: 5px;
    

}

.distance{
    font-size: 13px;
    font-weight: bold;

}

.freecancellaton{
    color: green;
    margin-top: 20px;
    font-size: 12px;
    font-weight: bold;
}

.content> button{
    background-color: blue;
    border: none;
    /* width: 300px; */
    height: 25px;
    color: white;
    font-size: 15px;
}

.content> button:hover{
    background-color: white;    
    color: blue;
    border: 2px solid blue;
    
}

@media screen and (max-width:425px) {

    .innerdiv{
        flex-direction: column;
    }

    .searchdiv{
        position: inherit;
    }

    .innerdiv>img{
        max-width: 400px;
    }
    
}