.mainlogindiv {
  background-color: black;
  /* height: 100vh; */
  display: flex;
  background-color: white;
    align-items: center;
  
  
}

.mainlogindiv img {
    width: 150px;

  }

  .row2 {
    width: max-content;
    margin: 40px auto;
    color: white;
    font-weight: 500;
    padding: 10px;
   /* border: 1px solid red; */
  
  }

    .formdiv {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      /* border: 1px solid red; */
      padding: 30px;
      gap: 12px;
      /* background-color: rgb(27, 26, 26); */
      background-color: blue;
    }

    .formdiv input {
        width: 250px;
        padding: 0 10px;
        height: 35px;
        font-size: 15px;
        font-family: serif;
        background-color: white;
        border: none;
        border-radius: 5px;
        color: rgb(48, 40, 40);
      }

      .formdiv button {
        width: 270px;
        background-color: white;
        border: none;
        height: 35px;
       color: blue;
        font-weight: 600;
        border-radius: 5px;

       
      }

      .row3 {
        font-size: 14px;
        
      }
    
      .privacystatement{
        font-size: 13px;
        text-align: center;
       border-top: 1px solid rgb(199, 187, 187);
       border-bottom: 1px solid rgb(199, 187, 187);
        width: 400px;
        margin: 0 auto;
        /* border: 2px solid red; */
      }

      .abc{
        font-size: 10px;
        display: flex;
        justify-content: center;
        margin-top: 30px;
      
      }

      .blue{
        color: blue;
        cursor: pointer;
      }

      @media screen and (max-width:320px) {

        .row2{
          padding: 0;
        }

        .formdiv{
          padding: 12px;
        }
        
      }






